<template>
  <div class='transfer' style='display: block'>
    <div style="text-align: center;">
      <a style="text-decoration: none; color: #c7088e" href="https://github.com/bitshares/beet"><h2>Beet - Your Blockchain Companion</h2></a>
      <p style="width: 400px; margin: auto;">
        In order to test this widget install Beet on your local computer, add an account of your desired blockchain
        and reload this page. Beet will ask if this app "Transfer Example" should be allowed to connect. After
        approving, your BinanceChain address should be correctly inserted in the "From" field. Fill out the rest and
        click "Push it", then you will see a popup in Beet asking for approval of the transfer.
      </p>
      <br />
      <p style="width: 400px; margin: auto;">
        This widget is closed source on purpose as it does not have to deal with private key security, and the user must
        approve any action. All private keys are safely stored within your locally running Beet and are NEVER exposed.
      </p>
    </div>
    <h1>Transfer</h1>
    <div>

    </div>
    <div
      class="nextToEachOther"
    >
      <p>Blockchain</p>
      <template v-for="chainKey in Object.keys(chainLogos)">
        <span
          :class="'chain-logo ' + (chainKey == chain ? 'chain-logo-active' : 'chain-logo-inactive')"
          v-bind:key="chainKey"
          v-html="chainLogos[chainKey]"
          @click="logoClick(chainKey)"
          :title="chainKey == chain ? 'Click to find out more about the ' + _getChain(chainKey).name : 'Click to transfer a token on the ' + _getChain(chainKey).name"
        >
        </span>
      </template>
      <br />
      <br />
      <input
        readonly
        id='blockchain'
        style='width: 320px'
        v-model='chainConfig[chain].name'
        type='text'
        placeholder='Blockchain to use'
      >
      <p>Token</p>
      <input
        readonly
        id='token'
        style='width: 320px'
        v-model='asset_id'
        type='text'
        placeholder='Token to transfer'
      >
      <p>From</p>
      <input
        id='from'
        style='width: 320px'
        v-model='from'
        type='text'
        placeholder='Link to Beet to use your account/address as sender'
      >
      <p>To</p>
      <input
        id='to'
        style='width: 320px'
        v-model='to'
        type='text'
        placeholder='Insert recipient account/address'
      >
      <p>Amount (float)</p>
      <input
        id='amount'
        style='width: 320px'
        v-model='amount'
        type='text'
        :placeholder="'Insert amount of ' + asset_id"
      >
    </div>
    <div
      class="nextToEachOther"
      style='vertical-align: top; margin-top: 25px'
    >
      <div style='margin-top: 20px' v-html="message">
      </div>
      <button
        class='transfer-button'
        style='margin-top: 0px'
        type='submit'
        @click='send'
        :title="'Click to send a transfer request to Beet, will need approval within Beet one more time before being broadcast to the ' + _getChain().name"
      >
        Push it
      </button>
    </div>
  </div>
</template>

<script>
import beet from '@beetapp/beet-js/src'
import {chainLogos} from '../assets/logos'

export default {
  name: 'HelloWorld',
  data () {
    return {
      chain: null,
      beetConnection: null,
      from: null,
      to: null,
      amount: null,
      asset_id: null,
      message: '&nbsp; ',
      //
      chainLogos: chainLogos,
      chainConfig: {
        BTS: {
          precision: 5,
          name: 'BitShares Blockchain',
          link: 'https://bitshares.org/',
          asset_visual: 'BTS',
          asset_id: '1.3.0'
        },
        BTC: {
          precision: 8,
          name: 'Bitcoin Blockchain',
          link: 'https://en.wikipedia.org/wiki/Bitcoin',
          asset_id: 'BTC'
        },
        BNB_TEST: {
          precision: 8,
          name: 'Binance Blockchain',
          link: 'https://testnet.binance.org/',
          asset_id: 'BNB'
        },
        EOS: {
          precision: 4,
          name: 'EOS Mainnet Blockchain',
          link: 'https://eos.io/',
          asset_id: 'EOS'
        },
        SMOKE: {
          precision: 3,
          name: 'Smoke Network Blockchain',
          link: 'https://smoke.io/',
          asset_id: 'SMOKE'
        },
        STEEM: {
          precision: 3,
          name: 'Steem Blockchain',
          link: 'https://steemit.com',
          asset_id: 'STEEM'
        },
        TLOS: {
          precision: 8,
          name: 'Telos Blockchain',
          link: 'https://telosfoundation.io/',
          asset_id: 'TLOS'
        },
        WLS: {
          precision: 3,
          name: 'WhaleShares Blockchain',
          link: 'https://whaleshares.io',
          asset_id: 'WLS'
        }
      }
    }
  },
  mounted () {
    this.logoClick('BNB_TEST')
  },
  methods: {
    _getChain (chain = null) {
      if (chain == null) {
        chain = this.chain
      }
      if (this.chain && this.chainConfig[this.chain]) {
        return this.chainConfig[this.chain].name
      } else {
        return {
          name: '',
          link: '',
          precision: 1,
          asset_id: ''
        }
      }
    },
    logoClick (chain) {
      if (this.chain === chain) {
        // open link and be done
        window.open(this.chainConfig[this.chain].link, '_blank')
        return
      }
      this.asset_id = this.chainConfig[chain].asset_visual || this.chainConfig[chain].asset_id
      this.chain = chain
      beet.beet.get('Transfer Example', this.chain).then(app => {
        this.app = app
        this.beetConnection = app[this.chain]
        this.from = this.beetConnection.getAccount().name
      }).catch(() => {
        this.from = 'Beet not found'
      })
    },
    send: async function () {
      try {
        if (this.from == null || this.to == null || this.amount == null) {
          this.message = 'Please fill in all fields'
          return
        }
        let result = await this.beetConnection.transfer(
          {
            to: this.to,
            amount:
              {
                satoshis: parseFloat(this.amount) * Math.pow(10, this.chainConfig[this.chain].precision),
                asset_id: this.chainConfig[this.chain].asset_id
              }
          }
        )
        this.message = result
        console.log(this.message)
      } catch (err) {
        this.message = err
      }
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style>

p {
  margin-bottom: 0.2rem;
}

.transfer-button {
  background-color: #c7088e;
  border: none;
  color: white;
  text-align: center;
  height: 8rem;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 8rem;
  border-radius: 50%;
  margin-top: 30px;
  margin-left: 0px;
  cursor: pointer
}
.transfer-button:hover {
  background-color: #a40a7d
}

.chain-logo {
  cursor: pointer;
}

.chain-logo > svg {
  height: 8rem;
  width: 8rem;
}

.chain-logo-inactive > svg {
  height: 2.5rem;
  width: 2.5rem;
}

.chain-logo-inactive > svg > g > path {
  fill: black;
}

.chain-logo-active > svg > g > path {
  fill: none;
}

.chain-logo-active {
  cursor: help;
}

.nextToEachOther2 {
  display: inline-block;
}

</style>



